#hangman-container {
    margin: 0 auto;
    position: relative;
    height: 250px;
    width: 250px;
    /* width: 20%; */
    cursor: pointer;
    --color: repeating-linear-gradient(white, gray, white);
    
}

#hangman-stake {
    width: 10%;
    height: 100%;
    background-image: url(data:image/png;base64,);
}

#hangman-stake::before {
    content: "";
    position: absolute;
    width: 100%;
    height: 10%;
    left: 0;
    background-image: url(data:image/png;base64,);
}

#hangman{
    position: absolute;
    height: 90%;
    width: 70%;
    top: 10%;
    right: 10%;
}

#hangman-head {
    width: 40%;
    height: 30%;
    background-image: url(data:image/png;base64,);
    -webkit-animation-duration: 1s;
            animation-duration: 1s;
    -webkit-animation-iteration-count: 1;
            animation-iteration-count: 1;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
    opacity: 0;
    border-radius: 50%;
    margin: 0 auto;
}

#hangman-body {
    width: 5%;
    height: 40%;
    /* position: absolute; */
    background-image: url(data:image/png;base64,);
    -webkit-animation-duration: 1s;
            animation-duration: 1s;
    -webkit-animation-iteration-count: 1;
            animation-iteration-count: 1;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
    opacity: 0;
    margin: 0 auto;
    position: relative;
    top: 0;
}

#hangman-leftarm {
    background-image: url(data:image/png;base64,);
    -webkit-animation-duration: 1s;
            animation-duration: 1s;
    -webkit-animation-iteration-count: 1;
            animation-iteration-count: 1;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
    opacity: 0;
    width: 40%;
    height: 5%;
    position: absolute;
    top: 35%;
    left: 12.5%;
}

#hangman-rightarm {
    background-image: url(data:image/png;base64,);
    -webkit-animation-duration: 1s;
            animation-duration: 1s;
    -webkit-animation-iteration-count: 1;
            animation-iteration-count: 1;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
    opacity: 0;
    width: 40%;
    height: 5%;
    position: absolute;
    top: 35%;
    right: 12.5%;
}

#hangman-leftleg {
    background-image: url(data:image/png;base64,);
    -webkit-animation-duration: 1s;
            animation-duration: 1s;
    -webkit-animation-iteration-count: 1;
            animation-iteration-count: 1;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
    opacity: 0;
    width: 50%;
    height: 5%;
    position: absolute;
    bottom: 15%;
    left: 7.5%;
    transform: rotate(-45deg)
}

#hangman-rightleg {
    background-image: url(data:image/png;base64,);
    -webkit-animation-duration: 1s;
            animation-duration: 1s;
    -webkit-animation-iteration-count: 1;
            animation-iteration-count: 1;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
    opacity: 0;
    width: 50%;
    height: 5%;
    position: absolute;
    bottom: 15%;
    right: 7.5%;
    transform: rotate(45deg)
}

