#guess-board {
    clear: right;
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
  }
  
  .letter-card {
    width: 75px;
    height: 75px;
    font-size: 50px;
    background-color: rgb(233, 230, 203);
    margin: 10px 10px;
    cursor: pointer;
    color: black;
    text-shadow: none;
    border-radius: 25% 0 25% 0;
    position: relative;
    background-image: linear-gradient(45deg, rgb(233, 230, 203), rgb(197, 192, 156));
    box-shadow: 2px 2px 2px rgb(53, 53, 53);
    transition-property: all;
    transition-duration: 0.2s;
  }
  
  .letter-card::before {
    content: "";
    position: absolute;
    background-color: red;
    width: 10px;
    height: 10px;
    border-radius: 50%;
    right: 5px;
    top: 5px;
  }
  
  .letter-card:hover {
    transform: scale(1.2);
  }
  
  .guessed-card::after {
    content: "X";
    color: red;
    width: 100%;
    height: 100%;
    position: absolute;
    left: 0;
    top: 0;
    font-size: 75px;
    line-height: 100%;
    font-weight: bolder;
  }
  
  @media only screen and (max-width: 1260px) {
    .letter-card {
        width: 45px;
        height: 45px;
        font-size: 35px;
        margin: 5px 5px;
    }

    .letter-card::before {
      width: 5px;
      height: 5px;
      right: 2.5px;
      top: 2.5px;
    }

    .guessed-card::after {
        font-size: 45px;
        line-height: 55px;
      }

    #guess-board {
        margin-top: 15px;
    }
  }

  @media only screen and (max-width: 480px) {
    .letter-card {
        width: 35px;
        height: 35px;
        font-size: 25px;
        margin: 5px;
    }

    .letter-card::before {
      width: 5px;
      height: 5px;
      right: 2.5px;
      top: 2.5px;
    }

    .guessed-card::after {
        font-size: 25px;
        line-height: 35px;
      }
  }
@-webkit-keyframes sway {
    0%{transform: translate(-50%, -150%);}
    /* 25%{transform: translate(-51%, -50%);}
    50%{transform: translate(-50%, -50%);}
    75%{transform: translate(-51%, -50%);} */
    100%{transform: translate(-50%, -50%);}
}


@keyframes sway {
    0%{transform: translate(-50%, -150%);}
    /* 25%{transform: translate(-51%, -50%);}
    50%{transform: translate(-50%, -50%);}
    75%{transform: translate(-51%, -50%);} */
    100%{transform: translate(-50%, -50%);}
}


#game-over {
    background-color: rgb(17, 19, 26);
    width: 75vw;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-image: url(/hangman-game/static/media/chalkboard.f5fdb27a.jpg);
    background-repeat: repeat;
    background-size: cover;
    border: 20px rgb(54, 44, 23) solid;
    box-shadow: 10px 10px 10px rgba(0, 0, 0, 0.466) inset, -10px -10px 10px rgba(0, 0, 0, 0.466) inset;
    -webkit-animation-duration: 1s;
            animation-duration: 1s;
    -webkit-animation-name: sway;
            animation-name: sway;
  }
  
  #game-over::before {
    content: "";
    width: 5px;
    height: 110%;
    background-color: white;
    position: absolute;
    top: -115%;
    left: 10%;
    background-image: repeating-linear-gradient(45deg,white,gray 5%,black 6%);
  }
  
  #game-over::after {
    content: "";
    width: 5px;
    height: 110%;
    background-color: white;
    position: absolute;
    top: -115%;
    right: 10%;
    background-image: repeating-linear-gradient(45deg,white,gray 5%,black 6%);
  }

@-webkit-keyframes bob {

  0%{transform: translate(0, 0px);}
  50%{transform: translate(0, 5px);}
  100%{transform: translate(0, 0px);}
}

@keyframes bob {

  0%{transform: translate(0, 0px);}
  50%{transform: translate(0, 5px);}
  100%{transform: translate(0, 0px);}
}

  #try-again-button {
      cursor: pointer;
      transition-property: all;
      transition-duration: 0.25s;
      width: 150px;
      margin: 20px auto;
      -webkit-animation: bob 2s infinite;
              animation: bob 2s infinite;
      -webkit-animation-delay: 0s;
              animation-delay: 0s;
  }

  #try-again-button:hover {
      transform: scale(1.2);
      -webkit-animation: none;
              animation: none;
  }

